import React from 'react';

const PrintPreview = ({ formData }) => {
  // 
  const getGenderText = (code) => {
    const genderMap = { '1': '', '2': '' };
    return genderMap[code] || '';
  };

  // 
  const formatDate = (dateStr) => {
    if (!dateStr) return '_____  _____  _____ ';
    const [year, month, day] = dateStr.split('-');
    return `${year}  ${month}  ${day} `;
  };

  return (
    <div className="print-preview">
      {/*  */}
      <style dangerouslySetInnerHTML={{ __html: `
        @media print {
          @page {
            size: A4 portrait;
            margin: 0.5cm;
          }
          
          body {
            margin: 0;
            padding: 0;
            -webkit-print-color-adjust: exact;
            print-color-adjust: exact;
            font-family: SimSun, "", sans-serif;
            font-size: 10pt;
          }
          
          .no-print { 
            display: none !important; 
          }
          
          /*  -  */
          .first-page {
            width: 100%;
            height: 270mm; /* A4297mm */
            position: relative;
            page-break-after: always;
            overflow: visible; /*  */
          }
          
          /*  -  */
          .second-page {
            width: 100%;
            height: 270mm;
            position: relative;
            page-break-before: always;
            display: flex;
            flex-direction: column;
            overflow: visible; /*  */
          }
          
          /*  */
          .certificate-title {
            text-align: center;
            font-weight: bold;
            font-size: 16pt;
            margin: 3mm 0;
          }
          
          .certificate-header {
            display: flex;
            justify-content: space-between;
            margin: 0 2mm 2mm 2mm;
            font-size: 10pt;
          }
          
          /*  */
          table {
            width: 100%;
            border-collapse: collapse;
            margin-bottom: 1mm;
            page-break-inside: avoid;
          }
          
          table, th, td {
            border: 1px solid black;
          }
          
          td {
            padding: 1mm;
            font-size: 9pt;
            height: auto;
            vertical-align: middle;
          }
          
          .bg-gray {
            background-color: #f0f0f0;
            font-weight: bold;
            text-align: center;
          }
          
          .hint-text {
            font-size: 6pt;
            color: #666;
            font-style: italic;
            margin-top: 0.5mm;
          }
          
          /*  */
          .certificate-part {
            margin-bottom: 3mm;
            border-bottom: 1px dashed #999;
            padding-bottom: 3mm;
          }
          
          .certificate-part:last-child {
            border-bottom: none;
            margin-bottom: 0;
          }
          
          /*  */
          .part-two {
            flex: 2;
          }
          
          .part-three {
            flex: 1.5;
          }
          
          .part-four {
            flex: 2;
          }
          
          /*  */
          .side-container {
            display: flex;
            width: 100%;
            border: 1px solid black;
          }
          
          .side-label {
            width: 20pt;
            border-right: 1px solid black;
            writing-mode: vertical-lr;
            transform: rotate(180deg);
            display: flex;
            align-items: center;
            justify-content: center;
            font-weight: bold;
            font-size: 10pt;
            padding: 3pt 0;
          }
          
          .side-content {
            flex: 1;
            padding: 1mm;
          }
          
          .side-title {
            text-align: center;
            font-weight: bold;
            font-size: 12pt;
            margin: 1mm 0;
          }
          
          /*  */
          .certificate-note {
            font-size: 6pt;
            margin-top: 1mm;
            font-style: italic;
          }
          
          /*  */
          .vertical-text {
            writing-mode: vertical-lr;
            transform: rotate(180deg);
            display: inline-block;
            white-space: nowrap;
            height: 100%;
          }
          
          .text-center {
            text-align: center;
          }
          
          .section-title {
            border-top: 1px solid #000;
            border-bottom: 1px solid #000;
            background-color: #f0f0f0;
            font-weight: bold;
            text-align: center;
            padding: 1mm 0;
            margin: 1mm 0;
            font-size: 10pt;
          }
          
          /*  */
          .standard-row td {
            height: 6mm;
          }
          
          .short-row td {
            height: 4mm;
          }
          
          .tall-row td {
            height: 8mm;
          }
          
          /*  */
          .first-page-content {
            border: 1px solid black;
            height: 100%;
          }
        }
      `}} />

      {/*  */}
      <div className="first-page">
        <div className="first-page-content">
          <h1 className="certificate-title"></h1>
          
          <div className="certificate-header">
            <div>
              <span>(): {formData.province || '_______'}</span>
              <span style={{ marginLeft: '10pt' }}>(): {formData.city || '_______'}</span>
              <span style={{ marginLeft: '10pt' }}>(): {formData.district || '_______'}</span>
            </div>
            <div>
              <span>: {formData.serialNumber || '_______'}</span>
            </div>
          </div>
          
          {/*  -  */}
          <table>
            <tbody>
              <tr className="standard-row">
                <td className="bg-gray" width="11%"></td>
                <td width="14%">{formData.deceasedName || ''}</td>
                <td className="bg-gray" width="8%"></td>
                <td width="12%">
                  {getGenderText(formData.gender) || ''}
                  <div className="hint-text">1.; 2.</div>
                </td>
                <td className="bg-gray" width="8%"></td>
                <td width="12%">{formData.ethnicity || ''}</td>
                <td className="bg-gray" width="13%"></td>
                <td width="22%">{formData.nationality || ''}</td>
              </tr>
              <tr className="standard-row">
                <td className="bg-gray"></td>
                <td colSpan="3">
                  {formData.idType === '1' ? '' : 
                   formData.idType === '2' ? '' : 
                   formData.idType === '3' ? '' : 
                   formData.idType === '4' ? '' : 
                   formData.idType === '5' ? '' : 
                   formData.idType === '6' ? '' : 
                   formData.idType === '7' ? '' : 
                   formData.idType === '8' ? '' : ''}
                  <div className="hint-text">1.; 2.; 3.; 4.; 5.; 6.; 7.; 8.</div>
                </td>
                <td className="bg-gray"></td>
                <td colSpan="3">{formData.idNumber || ''}</td>
              </tr>
              <tr className="standard-row">
                <td className="bg-gray"></td>
                <td>
                  {formData.age || ''}{' '}
                  {formData.ageUnit === '1' ? '' : 
                   formData.ageUnit === '2' ? '' : 
                   formData.ageUnit === '3' ? '' : 
                   formData.ageUnit === '4' ? '' : 
                   formData.ageUnit === '5' ? '' : ''}
                </td>
                <td className="bg-gray"></td>
                <td colSpan="2" className="text-center">{formatDate(formData.birthDate)}</td>
                <td className="bg-gray"></td>
                <td colSpan="2">
                  {formData.educationLevel === '1' ? '' :
                   formData.educationLevel === '2' ? '' :
                   formData.educationLevel === '3' ? '' :
                   formData.educationLevel === '4' ? '' :
                   formData.educationLevel === '5' ? '' :
                   formData.educationLevel === '6' ? '' :
                   formData.educationLevel === '7' ? '' :
                   formData.educationLevel === '8' ? '' : ''}
                  <div className="hint-text">1.; 2.; 3.; 4.; 5.; 6.; 7.; 8.</div>
                </td>
              </tr>
              <tr className="standard-row">
                <td className="bg-gray"></td>
                <td colSpan="3" className="text-center">
                  {formatDate(formData.deathDate)} {formData.deathTime ? formData.deathTime.split(':').join('  ') + ' ' : '_____  _____ '}
                </td>
                <td className="bg-gray"></td>
                <td colSpan="3">
                  {formData.deathPlace === '1' ? '' :
                   formData.deathPlace === '2' ? '' :
                   formData.deathPlace === '3' ? '' :
                   formData.deathPlace === '4' ? '' :
                   formData.deathPlace === '5' ? '' :
                   formData.deathPlace === '6' ? '' : ''}
                  <div className="hint-text">1.; 2.; 3.; 4.; 5.; 6.</div>
                </td>
              </tr>
              <tr className="standard-row">
                <td className="bg-gray"></td>
                <td colSpan="3">
                  {formData.personalIdentity === '11' ? '' :
                   formData.personalIdentity === '13' ? '' :
                   formData.personalIdentity === '17' ? '' :
                   formData.personalIdentity === '21' ? '' :
                   formData.personalIdentity === '24' ? '' :
                   formData.personalIdentity === '27' ? '' :
                   formData.personalIdentity === '31' ? '' :
                   formData.personalIdentity === '37' ? '' :
                   formData.personalIdentity === '51' ? '' :
                   formData.personalIdentity === '54' ? '' :
                   formData.personalIdentity === '70' ? '' :
                   formData.personalIdentity === '80' ? '' :
                   formData.personalIdentity === '90' ? '' : ''}
                  <div className="hint-text">11.; 13.; 17.; 21.; 24.; 27.; 31.; 37.; 51.; 54.; 70.; 80.; 90.</div>
                </td>
                <td className="bg-gray"></td>
                <td colSpan="3">{formData.workPlace || ''}</td>
              </tr>
              <tr className="standard-row">
                <td className="bg-gray"></td>
                <td colSpan="7">{formData.permanentAddress || ''}</td>
              </tr>
              <tr className="standard-row">
                <td className="bg-gray"></td>
                <td>{formData.relativeContact || ''}</td>
                <td className="bg-gray"></td>
                <td>{formData.relationship || ''}</td>
                <td className="bg-gray"></td>
                <td colSpan="3">{formData.contactPhone || ''}</td>
              </tr>
            </tbody>
          </table>
          
          {/*  */}
          <div className="section-title"></div>
          
          {/*  -  */}
          <table>
            <tbody>
              <tr className="standard-row">
                <td className="bg-gray" width="28%">. (a)</td>
                <td width="42%">{formData.directCause || ''}</td>
                <td className="bg-gray" width="30%"></td>
              </tr>
              <tr className="short-row">
                <td className="bg-gray">(b)(a)</td>
                <td>{formData.indirectCauseB || ''}</td>
                <td>{formData.indirectCauseBInterval || ''}</td>
              </tr>
              <tr className="short-row">
                <td className="bg-gray">(c)(b)</td>
                <td>{formData.indirectCauseC || ''}</td>
                <td>{formData.indirectCauseCInterval || ''}</td>
              </tr>
              <tr className="short-row">
                <td className="bg-gray">(d)(c)</td>
                <td>{formData.indirectCauseD || ''}</td>
                <td>{formData.indirectCauseDInterval || ''}</td>
              </tr>
              <tr className="standard-row">
                <td className="bg-gray">. </td>
                <td colSpan="2">{formData.otherDiagnosis || ''}</td>
              </tr>
            </tbody>
          </table>
          
          {/*  -  */}
          <table>
            <tbody>
              <tr className="standard-row">
                <td className="bg-gray" width="25%"></td>
                <td width="25%">
                  {formData.priorHospitalLevel === '1' ? '' :
                   formData.priorHospitalLevel === '2' ? '' :
                   formData.priorHospitalLevel === '3' ? '/' :
                   formData.priorHospitalLevel === '4' ? '' :
                   formData.priorHospitalLevel === '9' ? '' :
                   formData.priorHospitalLevel === '0' ? '' : ''}
                  <div className="hint-text">1.; 2.; 3./; 4.; 9.; 0.</div>
                </td>
                <td className="bg-gray" width="25%"></td>
                <td width="25%">
                  {formData.diagnosisBasis === '1' ? '' :
                   formData.diagnosisBasis === '2' ? '' :
                   formData.diagnosisBasis === '3' ? '' :
                   formData.diagnosisBasis === '4' ? '-' :
                   formData.diagnosisBasis === '5' ? '' :
                   formData.diagnosisBasis === '6' ? '' :
                   formData.diagnosisBasis === '9' ? '' : ''}
                  <div className="hint-text">1.; 2.; 3.; 4.-; 5.; 6.; 9.</div>
                </td>
              </tr>
              <tr className="standard-row">
                <td className="bg-gray"></td>
                <td colSpan="3">{formData.medicalHistory || ''}</td>
              </tr>
            </tbody>
          </table>
          
          <table>
            <tbody>
              <tr className="standard-row">
                <td className="bg-gray" width="20%"></td>
                <td width="30%">{formData.doctorSignature || ''}</td>
                <td className="bg-gray" width="20%"></td>
                <td width="30%">{formData.hospitalStamp || ''}</td>
              </tr>
              <tr className="short-row">
                <td className="bg-gray"></td>
                <td colSpan="3" className="text-center">
                  {formatDate(formData.fillDate)}
                </td>
              </tr>
              <tr className="short-row">
                <td className="bg-gray">()</td>
                <td colSpan="2">{formData.fundamentalCause || ''}</td>
                <td>ICD{formData.icdCode || ''}</td>
              </tr>
            </tbody>
          </table>
          
          {/*  */}
          <div className="section-title"></div>
          
          {/*  -  */}
          <table>
            <tbody>
              <tr>
                <td colSpan="4" style={{ height: '70px', verticalAlign: 'top', paddingTop: '2mm' }}></td>
              </tr>
              <tr>
                <td colSpan="4" style={{ textAlign: 'center', height: '20px' }}></td>
              </tr>
              <tr className="standard-row">
                <td className="bg-gray" width="20%"></td>
                <td width="30%">{formData.investigatedName || ''}</td>
                <td className="bg-gray" width="20%"></td>
                <td width="30%">{formData.relationToDeceased || ''}</td>
              </tr>
              <tr className="standard-row">
                <td className="bg-gray"></td>
                <td>{formData.investigatedPhone || ''}</td>
                <td className="bg-gray"></td>
                <td></td>
              </tr>
              <tr className="standard-row">
                <td className="bg-gray"></td>
                <td>{formData.deathJudgement === '1' ? '' : ''}</td>
                <td className="bg-gray"></td>
                <td className="text-center">
                  {formatDate(formData.investigationDate)}
                </td>
              </tr>
            </tbody>
          </table>
          
          <div style={{ textAlign: 'center', fontSize: '7pt', marginTop: '3mm' }}>
            ""<br/>
            "/"
          </div>
        </div>
      </div>

      {/*  -  */}
      <div className="second-page">
        {/*  */}
        <div className="certificate-part part-two">
          <div className="side-container">
            <div className="side-label"></div>
            <div className="side-content">
              <h2 className="side-title"></h2>
              
              <div className="certificate-header">
                <div>
                  <span></span>
                </div>
                <div>
                  <span>: {formData.serialNumber || '_______'}</span>
                </div>
              </div>
              
              <table>
                <tbody>
                  <tr>
                    <td className="bg-gray" width="12%"></td>
                    <td width="21%">{formData.deceasedName || ''}</td>
                    <td className="bg-gray" width="12%"></td>
                    <td width="21%">{getGenderText(formData.gender)}</td>
                    <td className="bg-gray" width="12%"></td>
                    <td width="22%">{formData.ethnicity || ''}</td>
                  </tr>
                  <tr>
                    <td className="bg-gray"></td>
                    <td>
                      {formData.idType === '1' ? '' : 
                      formData.idType === '2' ? '' : 
                      formData.idType === '3' ? '' : 
                      formData.idType === '4' ? '' : 
                      formData.idType === '5' ? '' : 
                      formData.idType === '6' ? '' : 
                      formData.idType === '7' ? '' : 
                      formData.idType === '8' ? '' : ''}
                    </td>
                    <td className="bg-gray"></td>
                    <td colSpan="3">{formData.idNumber || ''}</td>
                  </tr>
                  <tr>
                    <td className="bg-gray"></td>
                    <td className="text-center">
                        
                    </td>
                    <td className="bg-gray"></td>
                    <td className="text-center">
                        
                    </td>
                    <td className="bg-gray"></td>
                    <td>
                      {formData.deathPlace === '1' ? '' :
                      formData.deathPlace === '2' ? '' :
                      formData.deathPlace === '3' ? '' :
                      formData.deathPlace === '4' ? '' :
                      formData.deathPlace === '5' ? '' :
                      formData.deathPlace === '6' ? '' : ''}
                    </td>
                  </tr>
                  <tr>
                    <td className="bg-gray"></td>
                    <td colSpan="5">{formData.directCause || (formData.fundamentalCause || '')}</td>
                  </tr>
                  <tr>
                    <td className="bg-gray"></td>
                    <td colSpan="5">{formData.permanentAddress || ''}</td>
                  </tr>
                  <tr>
                    <td className="bg-gray"></td>
                    <td>{formData.relativeContact || ''}</td>
                    <td className="bg-gray"></td>
                    <td>{formData.relationship || ''}</td>
                    <td className="bg-gray"></td>
                    <td>{formData.contactPhone || ''}</td>
                  </tr>
                  <tr>
                    <td className="bg-gray"></td>
                    <td>{formData.doctorSignature || ''}</td>
                    <td className="bg-gray"></td>
                    <td colSpan="3"></td>
                  </tr>
                  <tr>
                    <td colSpan="3" className="text-center">
                      {formatDate(formData.fillDate).replace(/||/g, '')}
                    </td>
                    <td colSpan="3" className="text-center">
                        
                    </td>
                  </tr>
                </tbody>
              </table>
              
              <div className="certificate-note"></div>
            </div>
          </div>
        </div>
        
        {/*  */}
        <div className="certificate-part part-three">
          <div className="side-container">
            <div className="side-label"></div>
            <div className="side-content">
              <h2 className="side-title"></h2>
              
              <div className="certificate-header">
                <div>
                  <span></span>
                </div>
                <div>
                  <span>: {formData.serialNumber || '_______'}</span>
                </div>
              </div>
              
              <table>
                <tbody>
                  <tr>
                    <td className="bg-gray" width="12%"></td>
                    <td width="21%">{formData.deceasedName || ''}</td>
                    <td className="bg-gray" width="12%"></td>
                    <td width="21%">{getGenderText(formData.gender)}</td>
                    <td className="bg-gray" width="12%"></td>
                    <td width="22%">{formData.ethnicity || ''}</td>
                  </tr>
                  <tr>
                    <td className="bg-gray"></td>
                    <td>
                      {formData.idType === '1' ? '' : 
                      formData.idType === '2' ? '' : 
                      formData.idType === '3' ? '' : 
                      formData.idType === '4' ? '' : 
                      formData.idType === '5' ? '' : 
                      formData.idType === '6' ? '' : 
                      formData.idType === '7' ? '' : 
                      formData.idType === '8' ? '' : ''}
                    </td>
                    <td className="bg-gray"></td>
                    <td colSpan="3">{formData.idNumber || ''}</td>
                  </tr>
                  <tr>
                    <td className="bg-gray"></td>
                    <td className="text-center">
                        
                    </td>
                    <td className="bg-gray"></td>
                    <td className="text-center">
                        
                    </td>
                    <td className="bg-gray"></td>
                    <td>
                      {formData.deathPlace === '1' ? '' :
                      formData.deathPlace === '2' ? '' :
                      formData.deathPlace === '3' ? '' :
                      formData.deathPlace === '4' ? '' :
                      formData.deathPlace === '5' ? '' :
                      formData.deathPlace === '6' ? '' : ''}
                    </td>
                  </tr>
                  <tr>
                    <td className="bg-gray"></td>
                    <td colSpan="5">{formData.directCause || (formData.fundamentalCause || '')}</td>
                  </tr>
                  <tr>
                    <td className="bg-gray"></td>
                    <td colSpan="5">{formData.permanentAddress || ''}</td>
                  </tr>
                  <tr>
                    <td className="bg-gray"></td>
                    <td>{formData.relativeContact || ''}</td>
                    <td className="bg-gray"></td>
                    <td>{formData.relationship || ''}</td>
                    <td className="bg-gray"></td>
                    <td>{formData.contactPhone || ''}</td>
                  </tr>
                  <tr>
                    <td className="bg-gray"></td>
                    <td>{formData.doctorSignature || ''}</td>
                    <td className="bg-gray"></td>
                    <td colSpan="3"></td>
                  </tr>
                  <tr>
                    <td colSpan="3" className="text-center">
                      {formatDate(formData.fillDate).replace(/||/g, '')}
                    </td>
                    <td colSpan="3" className="text-center">
                        
                    </td>
                  </tr>
                </tbody>
              </table>
              
              <div className="certificate-note"></div>
            </div>
          </div>
        </div>
        
        {/*  */}
        <div className="certificate-part part-four">
          <div className="side-container">
            <div className="side-label"></div>
            <div className="side-content">
              <h2 className="side-title"></h2>
              
              <div className="certificate-header">
                <div>
                  <span></span>
                </div>
                <div>
                  <span>: {formData.serialNumber || '_______'}</span>
                </div>
              </div>
              
              <table>
                <tbody>
                  <tr>
                    <td className="bg-gray" width="12%"></td>
                    <td width="21%">{formData.deceasedName || ''}</td>
                    <td className="bg-gray" width="12%"></td>
                    <td width="21%">{getGenderText(formData.gender)}</td>
                    <td className="bg-gray" width="12%"></td>
                    <td width="22%">{formData.ethnicity || ''}</td>
                  </tr>
                  <tr>
                    <td className="bg-gray"></td>
                    <td>
                      {formData.idType === '1' ? '' : 
                      formData.idType === '2' ? '' : 
                      formData.idType === '3' ? '' : 
                      formData.idType === '4' ? '' : 
                      formData.idType === '5' ? '' : 
                      formData.idType === '6' ? '' : 
                      formData.idType === '7' ? '' : 
                      formData.idType === '8' ? '' : ''}
                    </td>
                    <td className="bg-gray"></td>
                    <td colSpan="3">{formData.idNumber || ''}</td>
                  </tr>
                  <tr>
                    <td className="bg-gray"></td>
                    <td className="text-center">
                        
                    </td>
                    <td className="bg-gray"></td>
                    <td className="text-center">
                        
                    </td>
                    <td className="bg-gray"></td>
                    <td>
                      {formData.deathPlace === '1' ? '' :
                      formData.deathPlace === '2' ? '' :
                      formData.deathPlace === '3' ? '' :
                      formData.deathPlace === '4' ? '' :
                      formData.deathPlace === '5' ? '' :
                      formData.deathPlace === '6' ? '' : ''}
                    </td>
                  </tr>
                  <tr>
                    <td className="bg-gray"></td>
                    <td colSpan="5">{formData.directCause || (formData.fundamentalCause || '')}</td>
                  </tr>
                  <tr>
                    <td className="bg-gray"></td>
                    <td colSpan="5">{formData.permanentAddress || ''}</td>
                  </tr>
                  <tr>
                    <td className="bg-gray"></td>
                    <td>{formData.relativeContact || ''}</td>
                    <td className="bg-gray"></td>
                    <td>{formData.relationship || ''}</td>
                    <td className="bg-gray"></td>
                    <td>{formData.contactPhone || ''}</td>
                  </tr>
                  <tr>
                    <td className="bg-gray"></td>
                    <td>{formData.doctorSignature || ''}</td>
                    <td className="bg-gray"></td>
                    <td colSpan="3"></td>
                  </tr>
                  <tr>
                    <td colSpan="3" className="text-center">
                      {formatDate(formData.fillDate).replace(/||/g, '')}
                    </td>
                    <td colSpan="3" className="text-center">
                        
                    </td>
                  </tr>
                </tbody>
              </table>
              
              <div className="certificate-note"></div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default PrintPreview; 