// DeathInfoSection.jsx - 
import React from 'react';

const DeathInfoSection = ({ register, errors }) => {
  return (
    <div className="death-info mb-6">
      <table className="w-full border-collapse border">
        <tbody>
          <tr>
            <td className="border p-2 bg-gray-50 w-[15%]"></td>
            <td className="border p-2" colSpan="2">
              <div className="flex">
                <input 
                  type="date" 
                  {...register('deathDate', { required: "" })} 
                  className="w-1/2"
                />
                <div className="flex items-center ml-4">
                  <label className="mr-2">:</label>
                  <input 
                    type="time" 
                    {...register('deathTime')} 
                    className="w-40"
                  />
                </div>
              </div>
              {errors.deathDate && <span className="text-red-500 text-xs">{errors.deathDate.message}</span>}
            </td>
            <td className="border p-2 bg-gray-50 w-[15%]"></td>
            <td className="border p-2" colSpan="2">
              <div className="grid grid-cols-3 gap-2">
                <label className="flex items-center">
                  <input type="radio" {...register('deathPlace')} value="1" />
                  <span className="ml-1"></span>
                </label>
                <label className="flex items-center">
                  <input type="radio" {...register('deathPlace')} value="2" />
                  <span className="ml-1"></span>
                </label>
                <label className="flex items-center">
                  <input type="radio" {...register('deathPlace')} value="3" />
                  <span className="ml-1"></span>
                </label>
                <label className="flex items-center">
                  <input type="radio" {...register('deathPlace')} value="4" />
                  <span className="ml-1"></span>
                </label>
                <label className="flex items-center">
                  <input type="radio" {...register('deathPlace')} value="5" />
                  <span className="ml-1"></span>
                </label>
                <label className="flex items-center">
                  <input type="radio" {...register('deathPlace')} value="6" />
                  <span className="ml-1"></span>
                </label>
              </div>
            </td>
          </tr>
          
          <tr>
            <td className="border p-2 bg-gray-50"></td>
            <td className="border p-2" colSpan="2">
              <input 
                type="text" 
                {...register('workPlace')} 
                className="w-full"
              />
            </td>
            <td className="border p-2 bg-gray-50"></td>
            <td className="border p-2" colSpan="2">
              <input 
                type="text" 
                {...register('permanentAddress')} 
                className="w-full"
              />
            </td>
          </tr>
          
          <tr>
            <td className="border p-2 bg-gray-50"></td>
            <td className="border p-2">
              <input 
                type="text" 
                {...register('relativeContact')} 
                className="w-full"
              />
            </td>
            <td className="border p-2 bg-gray-50 w-[10%]"></td>
            <td className="border p-2">
              <input 
                type="text" 
                {...register('relationship')} 
                className="w-full"
              />
            </td>
            <td className="border p-2 bg-gray-50 w-[10%]"></td>
            <td className="border p-2">
              <input 
                type="tel" 
                {...register('contactPhone', {
                  pattern: {
                    value: /^1[3-9]\d{9}$/,
                    message: ""
                  }
                })} 
                className="w-full"
              />
              {errors.contactPhone && <span className="text-red-500 text-xs">{errors.contactPhone.message}</span>}
            </td>
          </tr>
          
          {/*  */}
          <tr>
            <td className="border p-2 bg-gray-50" rowSpan="7"></td>
            <td className="border p-2 text-center font-medium bg-gray-100" colSpan="5"></td>
          </tr>
          <tr>
            <td className="border p-2 bg-gray-50" colSpan="2"></td>
            <td className="border p-2 bg-gray-50" colSpan="3"></td>
          </tr>
          <tr>
            <td className="border p-2 bg-gray-50">I. (a) </td>
            <td className="border p-2">
              <textarea 
                {...register('directCause')} 
                className="w-full h-20"
              ></textarea>
            </td>
            <td className="border p-2" colSpan="3">
              <input 
                type="text" 
                {...register('directCauseInterval')} 
                className="w-full"
                placeholder="321"
              />
            </td>
          </tr>
          <tr>
            <td className="border p-2 bg-gray-50">(b) (a)</td>
            <td className="border p-2">
              <textarea 
                {...register('indirectCauseB')} 
                className="w-full h-20"
              ></textarea>
            </td>
            <td className="border p-2" colSpan="3">
              <input 
                type="text" 
                {...register('indirectCauseBInterval')} 
                className="w-full"
              />
            </td>
          </tr>
          <tr>
            <td className="border p-2 bg-gray-50">(c) (b)</td>
            <td className="border p-2">
              <textarea 
                {...register('indirectCauseC')} 
                className="w-full h-20"
              ></textarea>
            </td>
            <td className="border p-2" colSpan="3">
              <input 
                type="text" 
                {...register('indirectCauseCInterval')} 
                className="w-full"
              />
            </td>
          </tr>
          <tr>
            <td className="border p-2 bg-gray-50">(d) (c)</td>
            <td className="border p-2">
              <textarea 
                {...register('indirectCauseD')} 
                className="w-full h-20"
              ></textarea>
            </td>
            <td className="border p-2" colSpan="3">
              <input 
                type="text" 
                {...register('indirectCauseDInterval')} 
                className="w-full"
              />
            </td>
          </tr>
          <tr>
            <td className="border p-2 bg-gray-50">II. </td>
            <td className="border p-2" colSpan="4">
              <textarea 
                {...register('otherDiagnosis')} 
                className="w-full h-20"
              ></textarea>
            </td>
          </tr>
          
          {/*  */}
          <tr className="bg-gray-50">
            <td className="border p-2 font-medium" colSpan="6"></td>
          </tr>
          <tr>
            <td className="border p-2 bg-gray-50"></td>
            <td className="border p-2" colSpan="2">
              <div className="grid grid-cols-3 gap-2">
                <label className="flex items-center">
                  <input type="radio" {...register('priorHospitalLevel')} value="1" />
                  <span className="ml-1"></span>
                </label>
                <label className="flex items-center">
                  <input type="radio" {...register('priorHospitalLevel')} value="2" />
                  <span className="ml-1"></span>
                </label>
                <label className="flex items-center">
                  <input type="radio" {...register('priorHospitalLevel')} value="3" />
                  <span className="ml-1">/</span>
                </label>
                <label className="flex items-center">
                  <input type="radio" {...register('priorHospitalLevel')} value="4" />
                  <span className="ml-1"></span>
                </label>
                <label className="flex items-center">
                  <input type="radio" {...register('priorHospitalLevel')} value="9" />
                  <span className="ml-1"></span>
                </label>
                <label className="flex items-center">
                  <input type="radio" {...register('priorHospitalLevel')} value="0" />
                  <span className="ml-1"></span>
                </label>
              </div>
            </td>
            <td className="border p-2 bg-gray-50"></td>
            <td className="border p-2" colSpan="2">
              <div className="grid grid-cols-3 gap-2">
                <label className="flex items-center">
                  <input type="radio" {...register('diagnosisBasis')} value="1" />
                  <span className="ml-1"></span>
                </label>
                <label className="flex items-center">
                  <input type="radio" {...register('diagnosisBasis')} value="2" />
                  <span className="ml-1"></span>
                </label>
                <label className="flex items-center">
                  <input type="radio" {...register('diagnosisBasis')} value="3" />
                  <span className="ml-1"></span>
                </label>
                <label className="flex items-center">
                  <input type="radio" {...register('diagnosisBasis')} value="4" />
                  <span className="ml-1">-</span>
                </label>
                <label className="flex items-center">
                  <input type="radio" {...register('diagnosisBasis')} value="5" />
                  <span className="ml-1"></span>
                </label>
                <label className="flex items-center">
                  <input type="radio" {...register('diagnosisBasis')} value="6" />
                  <span className="ml-1"></span>
                </label>
                <label className="flex items-center">
                  <input type="radio" {...register('diagnosisBasis')} value="9" />
                  <span className="ml-1"></span>
                </label>
              </div>
            </td>
          </tr>
          
          <tr className="bg-gray-50">
            <td className="border p-2 font-medium" colSpan="6"></td>
          </tr>
          <tr>
            <td className="border p-2 bg-gray-50"></td>
            <td className="border p-2" colSpan="2">
              <input 
                type="text" 
                {...register('doctorSignature')} 
                className="w-full"
              />
            </td>
            <td className="border p-2 bg-gray-50"></td>
            <td className="border p-2">
              <input 
                type="text" 
                {...register('hospitalStamp')} 
                className="w-full"
              />
            </td>
            <td className="border p-2">
              <div className="flex items-center">
                <span className="mr-2 whitespace-nowrap font-medium">:</span>
                <input 
                  type="date" 
                  {...register('fillDate')} 
                  className="w-full"
                />
              </div>
            </td>
          </tr>

          <tr>
            <td className="border p-2 bg-gray-50" colSpan="2"></td>
            <td className="border p-2" colSpan="2">
              <input 
                type="text" 
                {...register('fundamentalCause')} 
                className="w-full"
              />
            </td>
            <td className="border p-2 bg-gray-50">ICD</td>
            <td className="border p-2">
              <input 
                type="text" 
                {...register('icdCode')} 
                className="w-full"
              />
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  );
};

export default DeathInfoSection; 