import React, { useRef, useState } from 'react';
import { useForm } from 'react-hook-form';
import { useReactToPrint } from 'react-to-print';
import BasicInfoSection from './FormSections/BasicInfoSection';
import DeathInfoSection from './FormSections/DeathInfoSection';
import InvestigationSection from './FormSections/InvestigationSection';
import PrintPreview from './PrintPreview';
import './styles.module.css';

const DeathCertificateFormNew = () => {
  // 
  const { register, handleSubmit, watch, formState: { errors } } = useForm({
    defaultValues: {
      province: '',
      city: '',
      district: '',
      serialNumber: '',
      deceasedName: '',
      gender: '1', // 1
      idType: '1', // 1
      idNumber: '',
      nationality: '',
      ethnicity: '',
      age: '',
      ageUnit: '1', // 1
      birthDate: '',
      educationLevel: '6', // 6
      personalIdentity: '27', // 27
      deathDate: '',
      deathTime: '',
      deathPlace: '2', // 2
      workPlace: '',
      permanentAddress: '',
      relativeContact: '',
      relationship: '',
      contactPhone: '',
      directCause: '',
      directCauseInterval: '',
      indirectCauseB: '',
      indirectCauseBInterval: '',
      indirectCauseC: '',
      indirectCauseCInterval: '',
      indirectCauseD: '',
      indirectCauseDInterval: '',
      otherDiagnosis: '',
      priorHospitalLevel: '0', // 0
      diagnosisBasis: '5', // 5
      doctorSignature: '',
      hospitalStamp: '',
      fillDate: new Date().toISOString().split('T')[0], // 
      fundamentalCause: '',
      icdCode: '',
      medicalHistory: '',
      investigatedName: '',
      relationToDeceased: '',
      investigatedPhone: '',
      deathJudgement: '1', // 1
      investigatorSignature: '',
      investigationDate: new Date().toISOString().split('T')[0], // 
    }
  });
  
  // 
  const [previewMode, setPreviewMode] = useState(false);
  const [formData, setFormData] = useState({});
  
  //  - 
  const formRef = useRef();
  
  // 
  const onSubmit = data => {
    setFormData(data);
    setPreviewMode(true);
    // API
    console.log(':', data);
  };
  
  //  - PrintPreview
  const handlePrint = useReactToPrint({
    content: () => formRef.current,
    documentTitle: `_${formData.deceasedName || ''}`,
    onBeforeGetContent: () => {
      if (!previewMode) {
        setPreviewMode(true);
      }
      return Promise.resolve();
    },
    // 
    pageStyle: `
      @media print {
        /*  */
        .no-print {
          display: none !important;
        }
      }
    `
  });
  
  // 
  const backToEditMode = () => {
    setPreviewMode(false);
  };
  
  return (
    <div className="container mx-auto p-4">
      {/*  -  */}
      <div className="mb-4 flex gap-2 no-print">
        <button 
          onClick={backToEditMode} 
          className={`px-4 py-2 ${!previewMode ? 'bg-blue-500 text-white' : 'bg-gray-200'}`}
          disabled={!previewMode}
        >
          
        </button>
        <button 
          onClick={handlePrint} 
          className="px-4 py-2 bg-green-500 text-white"
        >
          
        </button>
      </div>

      {/* / */}
      {previewMode ? (
        <div ref={formRef} className="print-container no-border">
          <PrintPreview formData={formData} />
        </div>
      ) : (
        <form onSubmit={handleSubmit(onSubmit)} className="bg-white p-6 border rounded-lg shadow-md">
          <h1 className="text-center text-2xl font-bold mb-6"></h1>
          
          {/*  */}
          <div className="form-header grid grid-cols-2 gap-4 mb-6">
            <div className="flex items-center">
              <label className="mr-2">():</label>
              <input 
                type="text" 
                {...register('province', { required: "" })} 
                className="border px-2 py-1 flex-1"
              />
              <label className="mx-2">():</label>
              <input 
                type="text" 
                {...register('city')} 
                className="border px-2 py-1 flex-1"
              />
              <label className="mx-2">():</label>
              <input 
                type="text" 
                {...register('district')} 
                className="border px-2 py-1 flex-1"
              />
            </div>
            <div className="flex items-center justify-end">
              <label className="mr-2">:</label>
              <input 
                type="text" 
                {...register('serialNumber')} 
                className="border px-2 py-1 w-40"
              />
            </div>
          </div>
          
          {/*  */}
          <BasicInfoSection register={register} errors={errors} watch={watch} />
          
          {/*  */}
          <DeathInfoSection register={register} errors={errors} />
          
          {/*  */}
          <InvestigationSection register={register} errors={errors} />
          
          {/*  */}
          <div className="mt-6 text-center">
            <button 
              type="submit" 
              className="px-6 py-2 bg-blue-600 text-white rounded hover:bg-blue-700"
            >
              
            </button>
          </div>
        </form>
      )}
    </div>
  );
};

export default DeathCertificateFormNew; 